@import url(https://fonts.googleapis.com/css?family=Roboto:500);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* -------------------------- GLOBAL ----------------------------- */

body {
    font-family: 'Roboto', sans-serif;
    color: white;
}

/* -------------------------- HEADER ----------------------------- */

header {
    background-color: #484f8f;
    height: 50px;
    text-align: left;
    font-size: 25px;
}

#title {
    padding-top: 8px;
    padding-left: 15px;
}

/* -------------------------- RANGE SLIDER POSITIONING ----------------------------- */

.slider {
    margin-top: 25px;
    display: inline-block;
    color: black;
}

.slider label {
    margin-bottom: 3px;
    display: block;
    font-size: 17px;
}

.slider span {
    color: #484f8f;
    font-weight: 700;
    font-size: 17px;
    padding: 0px 15px 0px 15px;
}

.range-selection {
    margin: 0px 50px;
}

/* -------------------------- ARRAY BARS ----------------------------- */

.array-container {
    margin-top: 20px;
    height: 70vh;
}

.array-bar {
    background-color: #484f8f;
    display: inline-block;
    border-bottom: 5px solid #488F4B;
}

/* -------------------------- BUTTON APPEARANCE ----------------------------- */

button {
    font-family: 'Roboto', sans-serif;
    height: 35px;
    background-color: #488F4B;
    color: white;
    border: none;
    width: 120px;
    margin: 15px;
    border-radius: 3px;
    font-size: 17px;
    transition: all 0.3s;
}

button:hover:enabled {
    background-color: white;
    border: 2px solid #488F4B;
    color: #488F4B;
    cursor: pointer;
}

button:disabled {
    background-color: #AAAAAA;
    cursor: default;
}

.center-button {
    width: 200px;
}

#sort-button {
    background-color: #484f8f;
}

#sort-button:hover:enabled {
    background-color: white;
    border: 2px solid #484f8f;
    color: #484f8f;
}

#sort-button:disabled {
    background-color: #AAAAAA;
    cursor: default;
}

/* -------------------------- RANGE SLIDER APPEARANCE ----------------------------- */

input[type=range] {
    -webkit-appearance: none;
    width: 175px;
}

input[type=range]:focus {
    outline: none;
}

input[type=range]::-webkit-slider-runnable-track {
    height: 6px;
    cursor: pointer;
    box-shadow: 1px 1px 1px #000000;
    background: #484f8f;
    border-radius: 1.3px;
    border: 0.2px solid #010101;
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
}

input[type=range]:focus::-webkit-slider-runnable-track {
    background: #488F4B;
}

input[type=range]:disabled::-webkit-slider-runnable-track {
    background: #AAAAAA;
    box-shadow: 1px 1px 1px #777777;
    cursor: default;
}

input[type=range]::-webkit-slider-thumb {
    box-shadow: 1px 1px 1px #000000;
    border: 1px solid #000000;
    height: 23px;
    width: 16px;
    border-radius: 3px;
    background: #ffffff;
    cursor: pointer;
    -webkit-appearance: none;
    margin-top: -8px;
}

input[type=range]:disabled::-webkit-slider-thumb {
    box-shadow: none;
    border: 1px solid #777777;
    cursor: default;
}

input[type=range]::-moz-range-track {
    height: 6px;
    cursor: pointer;
    box-shadow: 1px 1px 1px #000000;
    background: #484f8f;
    border-radius: 1.3px;
    border: 0.2px solid #010101;
    -moz-transition: all 0.2s;
    transition: all 0.2s;
}

input[type=range]:disabled::-moz-range-track {
    background: #AAAAAA;
    box-shadow: .5px .5px .5px #777777;
    cursor: default;
}

input[type=range]::-moz-range-thumb {
    box-shadow: 1px 1px 1px #000000;
    border: 1px solid #000000;
    height: 18px;
    width: 12px;
    border-radius: 3px;
    background: #ffffff;
    cursor: pointer;
    -webkit-appearance: none;
    margin-top: -8px;
}

input[type=range]:disabled::-moz-range-thumb {
    box-shadow: none;
    border: 1px solid #777777;
    cursor: default;
}
  
